import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
  selector: 'app-pagina-equipes',
  templateUrl: './pagina-equipes.component.html',
  styleUrls: ['./pagina-equipes.component.css']
})
export class PaginaEquipesComponent implements OnInit {

  loading: boolean = true;

  constructor(private location: Location) { }

  ngOnInit() {
  }

  setLoadingStatus(status) {
    this.loading = status;
  }

  goBack() {
    this.location.back();
  }

}
