import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PaginaEquipesComponent } from './pagina-equipes.component';

const routes: Routes = [
  { path: '', component: PaginaEquipesComponent },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class PaginaEquipesRoutingModule { }
